.topbar {
  flex-direction: column;
  align-items: center;
  margin: 5px 15px;
  padding: 0px 10px;
}

#title {
  font-style: italic;
  height: 43px;
  z-index: 10;
  font-size: 28px;
  position: fixed;
  top: 0px;
  padding: 4px 0px;
  background-color: white;
}

.searching,
.navig {
  height: 38px;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 15px;
  background: #ebddf3;
  border-radius: 20px;
}
.searching {
  margin-top: 40px;
}
.navig {
  max-width: 450px;
}

#search-box {
  width: 77%;
  height: 100%;
  padding-left: 15px;
  background-color: transparent;
  font-size: 15px;
  border-radius: 20px;
}

#search-box:visited {
  background-color: #ebddf3;
}

#search-btn {
  width: 23%;
  height: 100%;
  margin-left: -20px;
}

.navs {
  flex: 1;
  background-color: transparent;
  height: 100%;
  font-size: 12.6px;
}
.pages {
  width: 120px;
  height: 35px;
  margin: 15px;
  margin-bottom: 50px;
}
